export enum Perfil {
    Cliente = 'Cliente',
    Recepcionista = 'Recepcionista',
    Especialista = 'Especialista',
    Administrador = 'Administrador'
}

export enum Especialidad {
    Oncologia = 'Oncologa',
    Dermatologia = 'Dermatologa',
    Odontologia = 'Odontologa',
    Psicologia = 'Psicologa'
}

export interface UsuarioInterface {
    Uid: string;
    Nombre: string;
    Email: string;
    Password: string;
    ImagenUrl?: string;
    Perfil?: Perfil;
    Especialidad?: Especialidad;
    Activo?: boolean;
}
